@import url('https://fonts.googleapis.com/css?family=Permanent+Marker');
@import url('https://fonts.googleapis.com/css?family=Open+Sans');

body {
    margin: 94px 12px;
    background: rgb(246,68,69);
    background: -webkit-gradient(linear, left top, right top, from(rgba(246,68,69,1)), color-stop(60%, rgba(171,56,111,1)), to(rgba(117,51,156,1)));
    background: -webkit-linear-gradient(left, rgba(246,68,69,1) 0%, rgba(171,56,111,1) 60%, rgba(117,51,156,1) 100%);
    background: -o-linear-gradient(left, rgba(246,68,69,1) 0%, rgba(171,56,111,1) 60%, rgba(117,51,156,1) 100%);
    background: linear-gradient(90deg, rgba(246,68,69,1) 0%, rgba(171,56,111,1) 60%, rgba(117,51,156,1) 100%);
    font-family: 'Open Sans', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.search-bar {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    color: #f0f0f0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    padding: 20px;
}

.search-bar > .fa-search {
    border-bottom: 1px solid #ccc;
    color: inherit;
    font-size: 20px;
    padding: 12px;
    opacity: 0.7;
}
.search-bar > input {
    opacity: 0.7;
    width: 600px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #ccc;
    outline: none;
    color: inherit;
    font-size: 28px;
}

.container {
    max-width: 600px;
    margin-top: 32px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: auto;
}

.card {
    background-color: #181818;
    color: #f0f0f0;
    -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
            box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    margin: 12px 0;
    padding: 20px 24px;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.card > .img-container {
    width: 30%;
    height: auto;
    margin-right: 20px;
}

.card > .img-container > img {
    width: 100%;
    height: auto;
}

.card > .right-side {
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.card .hero-name {
    margin: 0;
    font-family: 'Permanent Marker', cursive;
}


.spinner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.spinner > i{
    margin: 42px;
    display: none;
    -webkit-animation: spin 2s linear 0s infinite;
            animation: spin 2s linear 0s infinite;
    font-size: 32px;
    color: white;
    opacity: 0.7;
}

@-webkit-keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}



